@use 'base/variables' as v;
@use 'base/mixins' as m;


.formulario{
    margin-top: 5rem;
    width: 100%;
}

.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label{
        flex: 0 0 10rem;
    }

    input{
        border: none;
        border-radius: 1rem;
        flex: 1;
        padding: calc(v.$separacion / 4);

        &:focus{
            outline: none;
        }
    }
}
